export const formatPercent = (value) => Number(value).toLocaleString(undefined, { style: 'percent', maximumFractionDigits: 0 });
export const formatNumber = (value, options) => Number(value).toLocaleString(undefined, { maximumFractionDigits: 0, ...options });
export const formatUsd = (value, options) => Number(value).toLocaleString(undefined, {
    style: 'currency',
    currency: 'USD',
    maximumFractionDigits: 0,
    ...options
});
