import { isTruthy } from '../utils/isTruthy.js';
import { formatNumber, formatPercent } from '../utils/number.js';
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const tryWrap = (fn) => {
    return (...args) => {
        try {
            return fn(...args);
        }
        catch (e) {
            console.log(e);
            return null;
        }
    };
};
const calcB2bTat = tryWrap((complexity) => {
    const base = {
        low: [9, 19],
        medium: [20, 60],
        high: [45, 140]
    };
    const improvement = {
        low: [0.83, 0.71],
        medium: [0.83, 0.71],
        high: [0.83, 0.71]
    };
    const calcYRange = (index) => formatNumber(base[complexity][index] * (1 - improvement[complexity][index]));
    const Y = `${calcYRange(0)}-${calcYRange(1)}`;
    const X = `${formatPercent(improvement[complexity][1])}-${formatPercent(improvement[complexity][0])}`;
    return {
        elementId: '__TODO',
        text: `${X} faster deals, with the potential to reduce the sales cycle from weeks to just ${Y} days.`,
        X,
        Y,
        Z: null
    };
});
const calcB2bSellerProductivity = tryWrap((complexity, agreementVolume) => {
    const volume = Number(agreementVolume);
    const base = {
        low: [2, 6],
        medium: [6, 10],
        high: [8, 12]
    };
    const improvement = {
        low: [0.25, 0.5],
        medium: [0.25, 0.5],
        high: [0.25, 0.5]
    };
    const financial = {
        low: 25,
        medium: 25,
        high: 25
    };
    const calcYRange = (index) => formatNumber(base[complexity][index] * improvement[complexity][index] * volume);
    const Y = `${calcYRange(0)}-${calcYRange(1)}`;
    const X = `${formatPercent(improvement[complexity][0])}-${formatPercent(improvement[complexity][1])}`;
    const calcZRange = (index) => base[complexity][index] * financial[complexity] * improvement[complexity][index] * volume;
    return {
        elementId: '__TODO',
        text: `${X} more productive sellers, which frees up ${Y} annual hours to accelerate pipeline development, close more deals, defend price points, etc.`,
        X,
        Y,
        Z: [calcZRange(0), calcZRange(1)]
    };
});
const calcB2bReducedRevenueLeakage = tryWrap((complexity, rev) => {
    const revenue = Number(rev);
    const base = {
        low: [0.0375, 0.0475],
        medium: [0.0475, 0.0575],
        high: [0.0575, 0.0675]
    };
    const getBase = () => {
        const nonreductedBase = base[complexity];
        if (revenue < 250000000) {
            return nonreductedBase;
        }
        if (revenue < 1000000000) {
            return [nonreductedBase[0] - 0.01, nonreductedBase[1] - 0.01];
        }
        return [nonreductedBase[0] - 0.03, nonreductedBase[1] - 0.03];
    };
    const baseReducted = getBase();
    const improvement = {
        low: [0.05, 0.1],
        medium: [0.05, 0.1],
        high: [0.05, 0.1]
    };
    const X = `${formatPercent(improvement[complexity][0])}-${formatPercent(improvement[complexity][1])}`;
    const calcZRange = (index) => baseReducted[index] * improvement[complexity][index] * revenue;
    return {
        elementId: '__TODO',
        text: `${X} estimated reduction in revenue leakage by ensuring obligations are enforced, fees are collected, and renewal events are maximized.`,
        X,
        Y: null,
        Z: [calcZRange(0), calcZRange(1)]
    };
});
const calcB2bLegalCapacity = tryWrap((complexity) => {
    const improvement = {
        low: [0.78, 0.78],
        medium: [0.78, 0.78],
        high: [0.78, 0.78]
    };
    const X = `${formatPercent(improvement[complexity][0])}`;
    return {
        elementId: '__TODO',
        text: `Up to ${X} of agreements completed without legal intervention by establishing a self-service process with smart guardrails.`,
        X,
        Y: null,
        Z: null
    };
});
const calcB2bReducedLegalProductivity = tryWrap((complexity, agreementVolume) => {
    const volume = Number(agreementVolume);
    const base = {
        low: [0.5, 2],
        medium: [4, 8],
        high: [10, 15]
    };
    const improvement = {
        low: [0.5, 0.5],
        medium: [0.5, 0.5],
        high: [0.5, 0.5]
    };
    const financialConst = 78;
    const X = `${formatPercent(improvement[complexity][0])}`;
    const calcYRange = (index) => base[complexity][index] * improvement[complexity][index] * volume;
    const Y = `${formatNumber(calcYRange(0))}-${formatNumber(calcYRange(1))}`;
    const calcZRange = (index) => base[complexity][index] * improvement[complexity][index] * volume * financialConst;
    return {
        elementId: '__TODO',
        text: `Up to ${X} faster legal review and approvals, freeing up ${Y} annual hours to focus on more strategic negotiations, audits, etc.`,
        X,
        Y,
        Z: [calcZRange(0), calcZRange(1)]
    };
});
const calcB2bReducedRiskExposure = tryWrap((complexity, agreementVolume) => {
    const volume = Number(agreementVolume);
    const base = {
        low: [0.0025, 0.005],
        medium: [0.005, 0.0075],
        high: [0.0075, 0.01]
    };
    const improvement = {
        low: [0.05, 0.05],
        medium: [0.05, 0.05],
        high: [0.05, 0.05]
    };
    const financialConst = 100000;
    const X = `${formatPercent(improvement[complexity][0])}`;
    const calcZRange = (index) => base[complexity][index] * improvement[complexity][index] * volume * financialConst;
    return {
        elementId: '__TODO',
        text: `${X} estimated risk exposure reduction by ensuring agreements only contain standard, pre approved clauses unless there’s a legal-approved exception.`,
        X,
        Y: null,
        Z: [calcZRange(0), calcZRange(1)]
    };
});
export const calcB2b = (driverOption, characteristics) => {
    return [
        ...(driverOption.includes('B2B_1_seller_productivity')
            ? [
                calcB2bTat(characteristics.B2B_process_complexity[0]),
                calcB2bSellerProductivity(characteristics.B2B_process_complexity[0], characteristics.B2B_agreement_volume[0])
            ]
            : []),
        ...(driverOption.includes('B2B_3_reduced_revenue_leakage')
            ? [
                calcB2bReducedRevenueLeakage(characteristics.B2B_process_complexity[0], characteristics.B2B_revenue[0])
            ]
            : []),
        ...(driverOption.includes('B2B_2_reduced_risk_exposure')
            ? [
                calcB2bLegalCapacity(characteristics.B2B_process_complexity[0]),
                calcB2bReducedLegalProductivity(characteristics.B2B_process_complexity[0], characteristics.B2B_agreement_volume[0]),
                calcB2bReducedRiskExposure(characteristics.B2B_process_complexity[0], characteristics.B2B_agreement_volume[0])
            ]
            : [])
    ].filter(isTruthy);
};
